import { Component, h, Prop, Element } from '@stencil/core';
import { EVENTS_TYPES } from '../../utils/utils';
import { AddonService } from '../../services/service';

@Component({
  tag: 'neppochat-component',
  styleUrl: 'neppochat-component.css',
  shadow: true,
})
export class NeppoChatComponent {

  private neppoURL = "";
  private neppoToken = "";

  private addonService = new AddonService()
  private iframeHtml = "";

  @Element() private element: HTMLElement;
  /**
   * Parameters
   */
  @Prop() parameters: string;

  private createStatusOpeningListener(iframe: HTMLIFrameElement, event: string) {
    iframe.contentWindow.addEventListener(event, (msg: any) => {
      if (msg.detail.status == "snippet-opening") {
        this.openChat()
      }
      if (msg.detail.status == "snippet-closed") {
        this.closeChat()
      }
    });
  }

  private adjustLayout(iframe: HTMLIFrameElement) {
    const div = this.element.shadowRoot.querySelector('.neppo-chat-main-div') as HTMLElement;
    div.style.visibility = "hidden";
    div.style.opacity = "0";
    const widgetContainerElement = iframe.contentWindow.document.getElementById("widget-container");
    const element = iframe.contentWindow.document.querySelector("#chat-agent-main > app-widget > div") as HTMLElement;
    if (!widgetContainerElement || !element) {
      window.setTimeout(() => {
        this.adjustLayout(iframe)
      }, 1000);
      return;
    }

    const configEl = iframe.contentWindow.document.querySelector(".neppo-wrapper-config");
    if (configEl) {
      configEl.remove()
    }

    widgetContainerElement.style.height = "700px"
    element.style["align-items"] = "baseline";
    element.style["right"] = "auto";
    div.style.visibility = "visible"
    div.style.opacity = "1"
    div.style.transition = "visibility 0s linear 0s, opacity 300ms, width linear 500ms"
  }

  private openChat() {
    const div = this.element.shadowRoot.querySelector('.neppo-chat-main-div');
    div.classList.add("maximized-chat")
    div.classList.remove("minimized-chat")
  }

  private closeChat() {
    const div = this.element.shadowRoot.querySelector('.neppo-chat-main-div');
    div.classList.add("minimized-chat")
    div.classList.remove("maximized-chat")
  }

  async componentWillLoad() {
    try {
      const userData = await this.addonService.getUserData();
      if (userData.status == 200) {
        this.neppoURL = userData.data.responseBody.neppoURL + "/chat";
        this.neppoToken = userData.data.responseBody.neppoToken;
      }
    } catch (ex) {
      console.error("can't get userData", ex)
    }
    if (this.neppoToken == "" || this.neppoURL == "") {
      return;
    }
    return fetch(this.neppoURL + "/chat-agent/index.html")
      .then((response: Response) => response.text())
      .then(response => {
        response = response.replace("<base href=\"/chat/chat-agent/\">", `<base href="${this.neppoURL}/chat-agent/"`)
        response = response.replace("window.neppoHost = \"http://localhost:8080/chat\"", `window.neppoHost = "${this.neppoURL}"`)
        response = response.replace("window.neppoToken = \"\"", `window.neppoToken = "${this.neppoToken}"`)
        this.iframeHtml = response;
      });
  }

  render() {
    if (this.neppoToken == "" || this.neppoURL == "") {
      return;
    }
    return <div id="neppo-chat-agent" class="neppo-chat-main-div minimized-chat">
      <iframe id="neppo-chat-agent-iframe"
        class="neppo-main-iframe"
        sandbox="allow-same-origin allow-scripts allow-forms allow-popups"
        allow="autoplay; camera; microphone"
        scrolling="no"
        srcdoc={this.iframeHtml}
        onLoad={ev => this.onLoadIframe(ev)}>

      </iframe>
    </div>;
  }

  onLoadIframe(ev: Event) {
    this.createStatusOpeningListener(ev.target as HTMLIFrameElement, EVENTS_TYPES.STATUS_OPENING_SNIPPED)
    this.adjustLayout(ev.target as HTMLIFrameElement)
  }
}
