:host {
  display: block;
}

.neppo-chat-main-div {
  border: 0;
  background-color: transparent;
  pointer-events: none;
  z-index: 2147483639;
  position: fixed;
  bottom: 0;
  overflow: hidden;
  opacity: 1;
  left: 0;
  max-height: 100%;
}

.minimized-chat {
  width: 90px;
  height: 75px;
  transition: width 500ms;
}

.minimized-chat:hover.neppo-chat-main-div {
  width: 400px;
}

.maximized-chat {
  width: 400px;
  height: 785px;
}


.neppo-main-iframe {
  pointer-events: all;
  background: none;
  border: 0px;
  float: none;
  position: absolute;
  inset: 0px;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  min-height: 0px;
}